/* colors */
body {
  width: 100%;
  box-sizing: border-box;
}
html {
  box-sizing: border-box;
}
.visually__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOut {
  100% {
    opacity: 0;
  }
}
@keyframes shake {
  0%,
  100% {
    transform: translateX(0);
  }
  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translateX(-10px);
  }
  20%,
  40%,
  60%,
  80% {
    transform: translateX(10px);
  }
}
.header-block {
  font-family: "Galien", "Arial", sans-serif;
}
.header-block__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  position: relative;
}
.header-block__item {
  list-style: none;
}
.header-block__nav {
  max-width: 1366px;
  padding-left: 83px;
  padding-right: 83px;
  margin: 0 auto;
  padding-top: 23px;
}
@media (max-width: 767px) {
  .header-block__nav {
    padding-left: 25px;
    padding-right: 25px;
  }
}
.header-block__content {
  opacity: 0;
  list-style: none;
  right: 0;
  visibility: hidden;
  transition: visibility 0s 0.9s, opacity 0.6s ease;
  overflow: hidden;
}
.header-block__content--open {
  display: flex;
  flex-direction: column;
  transition: visibility 0s 0s, opacity 0.9s ease;
  opacity: 1;
  visibility: visible;
  position: absolute;
  top: 0;
  bottom: 0;
  right: -10px;
  clip-path: unset;
  clip: unset;
  overflow: unset;
  font-size: 18px;
  background-color: #5A98D0;
  z-index: 5;
  padding: 70px 50px 50px 70px;
  border-radius: 100px 0 100px 0;
  width: auto;
  height: max-content;
}
.header-block__link {
  color: #FFFFFF;
  text-decoration: none;
}
.header-block__burger {
  display: flex;
  justify-content: center;
  user-select: none;
  margin: 0;
  border-radius: 0;
  font-size: 0;
  line-height: 0;
  outline: 0;
  cursor: pointer;
  box-sizing: content-box;
  width: 20px;
  padding: 10px;
  padding-right: 0;
  background: 0 0;
  border: 0;
}
.header-block__toggle {
  position: relative;
  display: inline-block;
  width: 26px;
  height: 2px;
  vertical-align: bottom;
  transition: background 0.7s 0.7s;
  background-color: #356EAD;
}
.header-block__toggle::before,
.header-block__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background: #356EAD;
  transition: top 0.5s 0.5s, transform 0.5s, -webkit-transform 0.5s;
  transform-origin: 50% 50%;
}
.header-block__toggle::before {
  top: 8px;
}
.header-block__toggle::after {
  top: -8px;
}
.header-block__burger--open .header-block__toggle::before,
.header-block__burger--open .header-block__toggle::after {
  transition: top 0.3s, transform 0.3s 0.5s;
  width: 28px;
  top: 0;
  left: -5px;
}
.header-block__burger--open .header-block__toggle::before {
  transform: rotate3d(0, 0, 1, 45deg);
  background-color: #FFFFFF;
}
.header-block__burger--open .header-block__toggle::after {
  transform: rotate3d(0, 0, 1, -45deg);
  background-color: #FFFFFF;
}
.header-block__burger--open .header-block__toggle {
  background: 0 0;
  transition: background 0.1s 0.1s;
  z-index: 6;
}
