  opacity: 0;
  pointer-events: none;
  top: 1000px;
}
@media (max-width: 600px) {
  .modal-content__form {
    max-width: unset;
    width: unset;
    padding: 15px;
  }
}
.modal-content__body--noscroll {
  overflow: hidden;
