.modal-content {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  visibility: hidden;
  transition: background-color 0.3s ease-in-out, visibility 0s 0.75s;
}
.modal-content__open {
  display: flex;
  flex-direction: column;
  height: 100%;
  position: fixed;
  min-height: 100%;
  top: 0;
  width: 100%;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.5);
  font-family: "Gilroy", "Arial", sans-serif;
}
.modal-content__form--open {
  opacity: 1;
  pointer-events: all;
  transition: top 0.5s ease-in-out;
  animation: fadeIn 0.5s;
  box-shadow: 5px 10px 50px rgba(16, 112, 177, 0.2);
  position: relative;
}
.modal-content.modal-content__open {
  visibility: visible;
  transition: none;
}
.modal-content__body--noscroll .modal-content__form--open {
  top: 0;
}
.modal-content__title {
